/* src/css/index.css */

body {
    margin: 0;
    font-family: 'Manrope', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  background-color: #FBF6EE;
}

code {
  font-family: 'Manrope', sans-serif;
}

/* Container for the entire menu detail */
.menu-detail-container {
    background-color: #fdf6e3;
    color: #333;
}

.menu-header {
    margin-left: 4%;
    margin-right: 4%;
}

.menu-header h1{
    font-weight: 800;
    font-size: 40px;
    margin: 0;
    padding-bottom: 0.5vh;
}

.menu-header p{
    font-weight: 500;
    font-size: 18px;
    margin: 0;
    padding-bottom: 0.5vh;
}

.image-header{
    position: relative;
    display: inline-block;
    width: -webkit-fill-available;
}

.image-header .back-button{
    position: absolute; /* Position the button absolutely */
    top: 10px; /* Adjust the vertical position */
    left: 3%; /* Adjust the horizontal position */
    background: #fdf6e3; /* Circle background color */
    border: none; /* Remove default border */
    color: #000000; /* Text color */
    padding: 3px; /* Padding to create circular shape */
    border-radius: 50%; /* Make the button circular */
    display: flex; /* Use flexbox to center content */
    align-items: center; /* Center vertically */
    justify-content: center; /* Center horizontally */
    cursor: pointer;
}


.favorite-button {
    background-color: #fdf6e3;
    position: absolute;
    top: 12px;
    right: 10px;
    border: none;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: transform 0.2s ease-in-out;
    z-index: 10;
}

.favorite-button img {
    width: 26px;
    height: 26px;
}

.favorite-button:hover {
    transform: scale(1.2);
}


.text-container{
    display: flex;
    padding-left: 2%;
    padding-right: 2%;
    flex-direction: column;
    align-content: center;
    justify-content: flex-start;
    align-items: flex-start;
    font-family: 'Quicksand', sans-serif;
    font-weight: 500;
}

div.text-container div.header{
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: flex-start;
    width: -webkit-fill-available;
}

.header h2 {
    margin-bottom: 0;
    margin-left: 0;
    font-family: 'Quicksand', sans-serif;
    font-weight: 800;
}

/* Menu image styling */
.menu-image-large {
    display: block;
    width: 100%;
    margin: 0 auto;
    margin-bottom: 20px;
}

/* Ingredients section */
.menu-detail-container h2 {
    font-weight: 600;
    font-size: 3vh;
    color: #555;
}

.menu-detail-container ul {
    list-style: none;
    /* padding-bottom: 15vh; */
    margin: 0;
}

.menu-detail-container ul li {
    font-size: 18px;
    padding: 5px 0;
    border-bottom: 1px solid #ddd;
}

/* Steps section */
.menu-detail-container ol {
    padding-left: 20px;
    margin: 0;
}

.menu-detail-container ol li {
    font-size: 18px;
    margin: 10px 0;
}

.ingredientAndSeasoning-container {
    display: grid;                       /* ใช้ Grid Layout */
    grid-template-columns: repeat(3, 1fr); /* 3 คอลัมน์เท่าๆ กัน */
    grid-gap: 15px;
    gap: 15px;                            /* ระยะห่างระหว่างการ์ด */
    justify-items: center;                /* จัดการ์ดให้อยู่กลางคอลัมน์ */
    align-items: start;                   /* จัดแนวบน */
    margin: 0 auto;
    padding: 0 10px;
}

.ingredient-item {
    height: 130px;
    width: 100px;
    padding: 10px;
    background: #fff;
    border-radius: 12px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
    text-align: center;
}

/* Hover effect */
.menu-detail-container .ingredient-item:hover {
    transform: translateY(-2px);
    box-shadow: 0px 6px 16px rgba(0, 0, 0, 0.15);
}

/* Selected state styling */
.menu-detail-container .ingredient-item.selected {
    background-color: #fbb560;
    box-shadow: 0px 6px 20px rgba(130, 155, 65, 0.3);
    transform: translateY(-2px);
}

.ingredients-image {
    height: 80px;
    width: 80px;
    object-fit: contain;
}

.ingredient-item p {
    margin: 0;
    padding: 2px;
    font-size: 14px;
    font-weight: 500;
    text-align: center;
    /* object-fit: contain;  */
    max-width: 150px;
}

.ingredient-item p.header { 
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: default;
    font-size: 15px;
    font-weight: bold;
    max-width: 120px;
    width: 100%;
    margin: 0 auto;
}

/* Enhance text color when selected */
.ingredient-item.selected p.header {
    color: #ffffff;
}

.missing-ingredients {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-top: 40px;
    width: 100%;
}

.missing-text {
    font-family: 'Quicksand', sans-serif;
    margin: auto;
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 12px;
    color: #717070;
}

.add-to-list-button {
    font-family: 'Manrope';
    background-color: #829b41;
    color: #fff;
    border: none;
    border-radius: 12px;
    padding: 10px 30px;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out, transform 0.1s ease-in-out;
}

.add-to-list-button:hover {
    background-color: #50622a;
    transform: scale(1.05);
}

.add-to-list-button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
    transform: none;
}

.instructions-list {
    list-style: none;
    padding-right: 5%;
    font-family: 'Quicksand', sans-serif;
    margin: 0;
}

.instructions-list li {
    display: flex;
    align-items: center;
    font-size: 16px;
    /* margin-bottom: 16px; */
}

.checkbox-label {
    display: flex;
    align-items: flex-start;
    align-content: center;
    justify-content: center;
    flex-direction: row;
}

.custom-checkbox {
    display: none; /* Hide the default checkbox */
}

/* Style for the custom checkbox using images */
.custom-checkbox-styled {
    display: inline-block;
    margin-right: 10px;
    cursor: pointer;
}

.custom-checkbox-styled img {
    width: 24px;
    height: 24px;
}

/* Apply strikethrough when step is checked */
.checked-step {
    text-decoration: line-through;
    color: #888; /* Optional: Change color when checked */
}

.checkbox-label p{
    font-size: 15px;
}

ul.instructions-list li{
    margin-left: 0;
    font-size: 1rem;
    padding: 0%;
    border-bottom: 0px;
    margin-bottom: 2%;
}

.spliteline{
    border-bottom: 1px solid rgba(105, 124, 55, 0.5);
    padding-top: 4%;
    margin-left: 4%;
    margin-right: 4%;
    width: -webkit-fill-available;
}

.nutrition-container ul,
.nutrition-container li {
  list-style: disc !important;
  border: none !important;
  border-bottom: none !important;
  text-decoration: none !important;
  box-shadow: none !important;
  background: none !important;
  margin-left: 10px;
}

.tips-container, .nutrition-container {
  font-family: 'Quicksand', sans-serif;
}

.tips-container ul,
.tips-container li {
  list-style: none !important;
  border: none !important;
  border-bottom: none !important;
  text-decoration: none !important;
  box-shadow: none !important;
  background: none !important;
}

.tips-list {
  list-style: none;
  padding-left: 0;
}

.tip-item {
  display: flex;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
  margin-bottom: 2px;
  
}

.tip-icon {
  width: 20px;
  height: 20px;
  margin-left: 40px;

}

.allergy-popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center; 
  z-index: 1000;
}

.allergy-popup {
  background: white;
  border-radius: 16px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
  max-width: 90%;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center; 
  padding: 20px;
  overflow: hidden;
}

.allergy-popup-content {
  width: 100%;
  max-width: 500px;
  text-align: center; 
}

.allergy-popup-content h2 {
  color: #d32f2f;
  font-size: 24px;
  margin-bottom: 12px;
}

.allergy-popup-content p {
  margin-bottom: 12px;
  color: #333;
  font-size: 16px;
}

.allergy-list {
  margin: 0 auto 16px auto;
  padding: 0;
  list-style: none;
  max-height: 300px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.allergy-list li {
  margin-bottom: 12px;
  color: #d32f2f;
  font-size: 16px;
  font-weight: 600;
}

.allergy-confirm-btn {
  background-color: #d32f2f;
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  width: 100%;
  transition: background-color 0.3s;
}

.allergy-confirm-btn:hover {
  background-color: #b71c1c;
}

/* share dish container*/
.share-dish-container { 
    background-color: #ffff;
    display: flex;
    flex-direction: column; 
    align-items: center; 
    margin: 60px auto 0 auto; 
    padding: 0;
    width: 90%; 
    border-radius: 12px; 
    font-family: 'Quicksand', sans-serif; 
    font-size: 18px;
    font-weight: 600;
    color: #3f3f3f;
    box-shadow: 5px 5px 10px #cacaca;
    overflow: visible; 
    position: relative;
} 

.chef {
    width: 80%;
    height: auto;
    position: relative;
    top: 20px;
    margin-bottom: 10px;
    /*top: -40px;
    margin-bottom: -40px;*/
}


.dish-photo-buttons { 
    display: flex; 
    justify-content: center; 
    align-items: center; 
    margin-bottom: 20px; 
    grid-gap: 20px; 
    gap: 20px;
} 

.dish-take-photo-button, .dish-select-photo-button { 
    background-color: #FFA500; 
    border-radius: 10px; 
    border: none; 
    padding: 10px 20px; 
    color: white; 
    font-family: 'Quicksand', sans-serif; 
    font-weight: 600; 
    cursor: pointer; 
    transition: background-color 0.3s ease; 
} 

.dish-take-photo-button:active, .dish-select-photo-button:active { 
    background-color: #e39503; 
} 

.dish-submit-buttons { 
    display: flex; 
    justify-content: center; 
    align-items: center; 
    margin-bottom: 20px; 
    grid-gap: 20px; 
    gap: 20px; 
} 

.dish-share-button { 
    background-color: #B0BD39;  
    border-radius: 10px; 
    border: none; 
    padding: 10px 20px; 
    color: white; 
    font-family: 'Quicksand', sans-serif; 
    font-weight: 600; 
    cursor: pointer; 
    transition: background-color 0.3s ease; 
} 

.dish-retake-button { 
    background-color: #FFA500; 
    border-radius: 10px; 
    border: none; 
    padding: 10px 20px; 
    color: white; 
    font-family: 'Quicksand', sans-serif; 
    font-weight: 600; 
    cursor: pointer; 
    transition: background-color 0.3s ease; 
} 

.dish-image { 
    display: block; 
    margin: 20px auto; 
    height: auto; 
    border-radius: 12px; 
    object-fit: cover; 
}

.go-community-container {
    display: flex;
    justify-content: center;
    margin-top: 16px;
}

.go-community-button {
    background-color: #B0BD39;
    color: white;
    border: none;
    border-radius: 12px;
    padding: 20px;
    font-size: 16px;
    margin-bottom: 20px;
    font-weight: 600;
    cursor: pointer;
    transition: background-color 0.2s;
}

.go-community-button:hover {
    background-color: #ff8c00;
}

.dish-caption-box {
    width: 80%;
    margin-left: 12px;
    margin-bottom: 2px;
    font-size: 14px;
    border-radius: 8px;
    border: 1px solid #ccc;
    height: 20%;             
    padding-top: 16px;
    padding-left: 10px;         
    text-align: start;       
    display: flex;
    align-items: start;       
    justify-content: start;   
    box-sizing: border-box;
    resize: none;
    font-family: 'Quicksand', sans-serif;   
}

.dish-caption-box::placeholder {
    text-align: center; 
    color: #888;
    padding-left: 0;
    font-family: 'Quicksand', sans-serif; 
}

.dish-button-row {
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    margin-right: 12px;
}

.camera-container {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(0, 0, 0);
    padding: 0;
    margin: 0;
    z-index: 1000;
    overflow: hidden;
}


.camera-header {
    position: absolute;
    top: 1.75vh;
    left: 0;
    right: 0;
    display: flex;
    justify-content: flex-end; /* Keep the close button on the right */
    align-items: center;
    width: 100%;
    padding: 10px;
    background-color: transparent;
    z-index: 101;
    color: white;
    font-weight: bold;
}

.header-title {
    position: absolute;
    left: 50%;
    transform: translateX(-50%); /* Center the text horizontally */
    font-size: 2vh;
    color: #F4EEC7; /* The color for the header text */
}

.styled-button {
    background-color: transparent;
    border: none;
    padding-left: 20%;
    padding-right: 20%;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 1vw; /* Adjust size as needed */
    height: 1vh;
    z-index: 102;
  }

.camera-buttom .button-image {
    width: 8vw;
    height: 8vh;
    object-fit: contain; /* Ensure the SVG is properly contained within the button */
}

.webcam-view {
    flex: 1 1;
    width: 100%;
    height: auto;
    object-fit: cover;
    transform: scale(1);
}

.captured-image {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
}

.loading-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: white;
    height: 100vh;
}

.spinner {
    width: 50px;
    height: 50px;
    border: 5px solid rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    border-top-color: white;
    animation: spin 1s ease-in-out infinite;
    margin-bottom: 20px;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

.response-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: white;
    padding: 20px;
}

.camera-container .back-button {
    margin-top: 20px;
    background-color: rgba(0, 0, 0, 0.8);
    color: white;
    font-size: 18px;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
.camera-container .close-button{
    width: 2.25vh;
    height: 2.25vh;
    cursor: pointer;
    -webkit-padding-end: 10%;
            padding-inline-end: 10%;
}
.camera-buttom{
    position: fixed;
    bottom: 2.5%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;

}
.camera-buttom-preview{
    position: fixed;
    padding: 7vh;
    bottom: 0%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100vw;

}
.camera-buttom-preview .button-image {
    width: 30vw;
    height: 30vh;
    object-fit: contain; /* Ensure the SVG is properly contained within the button */
}
.camera-button {
    background-color: transparent; /* Remove background */
    border: none; /* Remove default border */
    cursor: pointer;
    z-index: 102;
    border-radius: 50%; /* Ensuring the button remains circular */
}

.camera-button-img {
    width: 10vh;
    height: 10vh;
    object-fit: cover;
}

  
.upload-image{
    padding: 0.15vh;
    width: 4.25vh;
    height: 4.25vh;
    background-color: rgba(104, 101, 101, 0.7);
    border-radius: 25%; 
    
}
.upload-container{
    padding-left: 10%;
}
.flash-button{
    display: none;
}   
.swap-camera{
    background-color: rgba(104, 101, 101, 0.7);
    border-radius: 100%; 
    width: 4vh;
    height: 4vh;
    padding: 0.5vh;
}

.swap-container{
    padding-right: 10%;
}
/* Preferences Page Container */

.preferences-container{
  font-family: 'Manrope', sans-serif;
  padding: 20px;
}

.menu-header-container h1 {
    font-size: 2.5vh;
    display: flex;
    flex-direction: row;
    align-items: center;
}

/* Header */
.preferences-container .header {
  align-items: flex-start;
  font-size: 2.5vh;
  margin: 3px 0 15px 0;
  font-weight: bold;
  font-family: 'Manrope', sans-serif;
}

.back-button::before {
  content: '←';
  font-size: 4.5vh;
  font-weight: 600;
  margin-bottom: 6%;
}

.back-button:hover {
    color: #000;
}
  
/* Preference List Styling */
.preference-list {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 10px;
  gap: 10px;
  margin: 0;
}

/* Preference Item (Button) */
.preference-item {
  font-family: 'Manrope', sans-serif;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 20px;
  padding: 1vh 2vh;
  cursor: pointer;
  font-size: 1.5vh;
  transition: background-color 0.3s ease;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
}

/* Preference Item when selected */
.preference-item.selected {
  background-color: #A8D671;  /* Light green when selected */
  border-color: #A8D671;
  color: white;
}

/* Search Button */
.search-button {
  font-family: 'Manrope', sans-serif;
  font-weight: bold;
  background-color: #FFA500;  /* Orange button */
  color: white;
  font-size: 1.5vh;
  margin-top: 20px;
  padding: 10px;
  border: none;
  border-radius: 25px;
  width: 100%;
  cursor: pointer;
}

/* Permanent Filter Label */
.permanent-filter {
  font-family: 'Manrope', sans-serif;
  font-size: 1.25vh;
  color: #888;
}
  

/* MenuSuggestion.css */

.menu-suggestion-container {
    padding-left: 4.5%;
    padding-right: 4.5%;
    background-color: #fdf7f1;
    min-width: auto;
    margin: 0 auto;
    border-radius: 16px;
    min-height: 100%;
    display: flex;
    flex-direction: column;
}

.menu-suggestion-container h1 {
    font-size: 3vh;
    font-weight: 600;
    margin-bottom: 3vh;
    margin-top: 3vh;
    color: #333;
    text-align: left; /* Left-align title like in the example */
}
.menu-header-container{
    padding-top: 3.75vh;
    padding-bottom: 0.375vh;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.menu-list {
    list-style-type: none;
    padding: 0;
    margin: 0;
    flex-grow: 1; /* This ensures the list stretches to fill available space */
}

.menu-item {
    display: flex;
    align-items: flex-start;
    margin-bottom: 3vh;
    padding-bottom: 3vh;
    padding-top: 1.5vh;
    border-bottom: 1px solid rgba(105, 124, 55, 0.5);
    transition: transform 0.1s;
}

.menu-item:hover {
    transform: translateY(-2px);
}

.menu-image {
    margin-left: 1.5vh;
    flex-shrink: 0; /* Prevent the image from shrinking */
    width: 15vh;
    height: 15vh;
    object-fit: cover;
    margin-right: 3vh; /* Add some space between the image and the text */
    border-radius: 20px;
    border: 1px solid #e6e6e6;
