  display: flex;
  white-space: nowrap;
  grid-gap: 10px;
  gap: 10px;
}

.shopping-items {
  padding-top: 2rem;
  display: flex;
  flex-wrap: wrap;
  grid-gap: 12px;
  gap: 12px;
  margin: 20px 0;
}

.shopping-card {
  background: #fff;
  border-radius: 12px;
  padding: 12px;
  width: 100px;
  text-align: center;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  position: relative;
}

.shopping-image {
  width: 60px;
  height: 60px;
  object-fit: contain;
  margin-bottom: 8px;
}

.empty-text {
  text-align: center;
  font-size: 16px;
  color: #777;
  margin-top: 40px;
}

/* ปุ่ม Order */
.order-button {
  font-family: 'Manrope', sans-serif;
  position: fixed;      
  bottom: 20px;         
  left: 50%;            
  transform: translateX(-50%); 
  padding: 12px 80px;
  border-radius: 20px;
  font-weight: bold;
  background-color: #FE9801;
  color: white;
  border: none;
  cursor: pointer;
  z-index: 1000;  
}

/* Popup overlay */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Popup content */
.popup-content {
  font-family: 'Manrope', sans-serif;
  color: #777776;
  background: rgb(255, 250, 241);
  padding: 24px;
  border-radius: 12px;
  text-align: center;
  width: 300px;
}

.popup-buttons {
  margin: auto;
  display: flex;
  margin-top: 20px;
  margin-bottom: 30px;
  justify-content: center;
}

.sponsor-button img {
  width: 100px;
  height: 80px;
  object-fit: contain;
}

.sponsor-button {
  display: flex;
  flex-direction: row;
  align-items: center;
  border: none;
  cursor: pointer;
}

.sponsor-button.lineman {
  background-color: rgb(255, 250, 241); /* LINE MAN */
}

.sponsor-button.grab {
  background-color: rgb(255, 250, 241); /* Grab */
}

.close-button {
  font-family: 'Manrope', sans-serif;
  background: gray;
  color: white;
  padding: 8px 30px;
  border: none;
  border-radius: 20px;
  cursor: pointer;
}

.remove-item {
    position: absolute;
    top: 5px;
    right: 5px;
    background: #ff6b6b;
    color: white;
    border: none;
    border-radius: 50%;
    width: 24px;
    height: 24px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
}

.remove-item:hover {
    background: #ff5252;
}




/* Container */
.community-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 0 20px;
    padding-bottom: 80px;
    box-sizing: border-box;
}

/* Header */
.community-header-container {
  position: relative;
  width: 100%;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 12px;
}

.community-header-image {
  width: 65%;
  height: 120px;
  object-fit: cover;
  display: block;
}


/* No posts text */
.community-no-posts {
  text-align: center;
  color: #777;
  margin: 24px 0;
  font-size: 16px;
}

.back-to-menu-detail {
  position: absolute;
  top: 16px;
  left: 16px;

}

/* Shared dish preview (when coming from MenuDetail) */
.shared-dish-preview {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 8px;
  gap: 8px;
  margin: 12px 0;
  padding: 10px;
  background: #ffffff;
  border-radius: 10px;
  box-shadow: 0 6px 18px rgba(37,37,37,0.04);
}
.shared-dish-preview h3 {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
}
.shared-dish-image {
  width: 100%;
  max-width: 420px;
  height: auto;
  border-radius: 10px;
  object-fit: cover;
  box-shadow: 0 6px 12px rgba(0,0,0,0.06);
}

/* Post card */
.community-post-card {
  background: #fff;
  width: 100%;
  border-radius: 12px;
  margin-top: 8px;
  margin-bottom: 8px;
  padding: 12px;
  box-shadow: 0 6px 18px rgba(32,32,32,0.04);
  transition: transform .12s ease, box-shadow .12s ease;
}
.community-post-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 28px rgba(32,32,32,0.06);
}

/* Post header */
.community-post-header {
  display: flex;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
  margin-bottom: 8px;
}
.user-profile-pic {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #fff;
  box-shadow: 0 4px 10px rgba(0,0,0,0.06);
}
.community-username {
  font-weight: 700;
  font-size: 18px;
  margin: 0;
  color: #FFA500;
  font-family: 'Quicksand', sans-serif;
}

/* Post image */
.community-post-image {
  width: 100%;
  max-height: 380px;
  margin-top: 8px;
  margin-bottom: 10px;
  object-fit: cover;
  border-radius: 10px;
  display: block;
}

/* Actions */
.community-post-actions {
  display: flex;
  align-items: center;
  grid-gap: 8px;
  gap: 8px;
  margin-bottom: 2px;
}

/* Like button */
.community-like-button {
  display: flex;
  align-items: center;
  justify-content: center; 
  margin-left: 14px;
  margin-bottom: 8px;
  grid-gap: 6px;
  gap: 6px; 
  padding: 6px 12px;
  border-radius: 8px;
  background: #fff;
  cursor: pointer;
  font-weight: 600;
  font-size: 14px;
  color: #333;
  transition: all .12s ease;
  border: none;
  outline: none;
}

/* หัวใจ */
.community-like-button img {
  width: 22px;
  height: 22px;
  filter: none;
  margin: 0; 
}


.community-like-button.liked {
  background: linear-gradient(90deg,#ff7a59,#FFA500);
  color: #fff;
  border: none;
  padding: 6px 12px;
}


.community-like-button.liked img {
  filter: brightness(0) invert(1);
}

.community-like-count {
  font-weight: 600;
  font-size: 16px;    
  color: #000;
  margin: 0; 
  display: flex;
  align-items: center;
}

.community-username-caption {
  font-weight: bold;
  margin-right: 8px;
  font-family: 'Quicksand', sans-serif;
}

.community-caption {
  margin: 8px 20px 4px 20px;
  font-family: 'Quicksand', sans-serif;
}

.community-created-at {
  color: #8b8b8b;
  font-size: 12px;
  margin-left: 20px;
}
