/* src/css/Navbar.css */
.navbar {
  position: fixed;
  bottom: 2.5%;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #4F4A45;
  padding-left: 3.5vw;
  padding-right: 3.5vw;
  padding-top: 1.2vh;
  padding-bottom: 1.2vh;
  border-radius: calc(1vh + 5vmin);
  width: 78vw;
  height: 5vh;
  margin: auto;
}

.nav-item {
  color: #ffffff;
  font-size: 20px;
  padding: 0 10px;
  position: relative; /* Required for absolute positioning of the pseudo-element */
}

.nav-item img {
  width: 7.5vw;
  height: 5vh;
  transition: transform 0.3s ease, fill 0.3s ease;
}

.nav-item:hover img {
  transform: scale(1.1);
}

.nav-item.selected img {
  filter: brightness(0) saturate(100%) invert(56%) sepia(57%) saturate(1274%) hue-rotate(359deg) brightness(103%) contrast(100%);
}

.nav-item.selected::after {
  content: '';
  border-radius: calc(40vh + 5vmin);
  position: absolute;
  bottom: -5px; 
  transform: translateX(-50%);
  width: 60vw;
  height: 0.4vh;
  background-color: #FE9801;
}

.home.nav-item.nav-item.selected::after {
  left: 50%;
  width: 75%;
}
.heart.nav-item.nav-item.selected::after {
  left: 50%;
  width: 70%;
}
.history.nav-item.nav-item.selected::after {
  left: 50%;
  width: 75%;

}
.account.nav-item.nav-item.selected::after {
  left: 50%;
  width: 60%;
}
.icon {
  padding: calc(1vh + 1vmin);
  font-size: calc(16vh + 1vmin);
}

